import { Component, OnInit,Input,OnChanges} from '@angular/core';
import {CusViewHttpService} from '../../../http/custom-view/custom-view.http.service';
import {Message} from 'primeng/primeng';
import {CreditLineBean} from '../../bean/custom-view.bean';
// import {ZhStatusType,Flag,UseType,TranCategory,QianFaClass,NewBillStatus,BussStatus,GuaranteeMode,PayType,COUNTRY} from '../../../constant/custView.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//综合授信额度
@Component({
  selector: 'credit-line-detail',
  templateUrl: './credit-line-detail.component.html',
  styleUrls: ['./credit-line-detail.component.scss']
})
export class CreditLineDetailComponent implements OnInit,OnChanges {
  @Input() inValue:any;
  msgs:Message[]=[];  
  creditLineDetail:CreditLineBean=new CreditLineBean();
  guaranteeModeOptions:any[]=[];//担保方式
  zhStatusTypeOptions:any[]=[];//状态
  flagOptions:any[]=[];//标志
  useTypeOptions:any[]=[];//使用类型
  tranCategoryOptions:any[]=[];//业务种类编码
  code:any;  
  constructor(
    public httpService:CusViewHttpService,
    public commfunc: Commfunc
  ) { 
    this.codeVal();
    this.guaranteeModeOptions=this.code['GuaranteeMode'];
    this.zhStatusTypeOptions=this.code['ZhStatusType'];
    this.flagOptions=this.code['Flag'];
    this.useTypeOptions=this.code['UseType'];
    this.tranCategoryOptions=this.code['TranCategory'];
    //-----------------------------------上面是码值
  }

  ngOnInit() {
  }
  ngOnChanges(){
    if(this.inValue){
      this.httpService.BZ_0200300000202_LOAN({number:this.inValue.number}).subscribe(data=>{
        if(data.returnCode.code=='success'){
          this.creditLineDetail=data;
          this.httpService.queryTellerById({tellerId:this.creditLineDetail.investigatorName}).subscribe(data=>{
            this.creditLineDetail.investigatorName=data.tellerName;
          });
          this.httpService.queryOrgById({orgId:this.creditLineDetail.appBranchNo}).subscribe(data=>{
            if(data.orgName!=null){
              this.creditLineDetail.appBranchNo=data.orgName;              
            }
          });
        }else{
          data.returnCode.message ? data.returnCode.message : '调用服务失败';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      })
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
