import { Component, OnInit,Input,OnChanges} from '@angular/core';
import {GuaranteeDetailBean} from '../../bean/custom-view.bean';
import {CusViewHttpService} from '../../../http/custom-view/custom-view.http.service';
import {Message} from 'primeng/primeng';
// import {BhZt,StatusType,CURRENCY,BussType,GuaranteeMode,BaoHanClass,PayType,TenClass,PayTypeBH} from '../../../constant/custView.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//保函详情
@Component({
  selector: 'guarantee-detail',
  templateUrl: './guarantee-detail.component.html',
  styleUrls: ['./guarantee-detail.component.scss']
})
export class GuaranteeDetailComponent implements OnInit,OnChanges {
  @Input() inValue:any;
  msgs:Message[]=[];  
  guaranteeDetail:GuaranteeDetailBean=new GuaranteeDetailBean();
  bussTypeOptions:any[]=[];//业务类型
  guaranteeModeOptions:any[]=[];//担保方式
  baoHanClassOptions:any[]=[];//保函类型
  // payTypeOptions:any[]=PayType;//支付类型
  tenClassOptions:any[]=[];//十级分类
  currencyOptions:any[]=[];//币种
  statusTypeOptions:any[]=[];//保函状态
  payTypeOptions:any=[];//支付类型
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc: Commfunc    
  ) { 
    this.codeVal();
    this.bussTypeOptions=this.code['BussType'];
    this.guaranteeModeOptions=this.code['GuaranteeMode'];
    this.baoHanClassOptions=this.code['BaoHanClass'];
    this.tenClassOptions=this.code['TenClass'];
    this.currencyOptions=this.code['CURRENCY'];
    this.statusTypeOptions=this.code['BhZt'];
    this.payTypeOptions=this.code['PayTypeBH'];
    //---------------------------------------上面是码值
  }

  ngOnInit() {
  }
  ngOnChanges(){
    if(this.inValue){
      this.httpService.BZ_0200300000402_LOAN({contractNo:this.inValue.contractNo}).subscribe(data=>{
        if(data.returnCode.code=='success'){
          this.guaranteeDetail=data;
          this.httpService.queryTellerById({ tellerId: this.guaranteeDetail.investigatorName }).subscribe(item => {
            this.guaranteeDetail.investigatorName = item.tellerName;
          });
          this.httpService.queryTellerById({ tellerId: this.guaranteeDetail.liablePersonName }).subscribe(item => {
            this.guaranteeDetail.liablePersonName = item.tellerName;
          });
          this.httpService.queryOrgById({orgId:data.appBranchNo}).subscribe(item=>{
            if(item.orgName!=null){
              data.appBranchNo=item.orgName;                              
            }
          });
          this.httpService.queryOrgById({orgId:data.manageBranchId}).subscribe(item=>{
            if(item.returnCode.code=='success'){
              if(item.orgName!=null){
                data.manageBranchId=item.orgName;                              
              }
            }
          });
        }else{
          data.returnCode.message ? data.returnCode.message : '调用服务失败';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      });
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
