  // 批量预授信查询this.batchCreditBean.operType = "2";
  getBatchList() {
    this.batchListPageNum = this.batchListPageNum || 1,
      this.batchListPageSize = this.batchListPageSize || 10
    let param = {
      custNo: this.custNo,
      pageNum: this.batchListPageNum,
      pageSize: this.batchListPageSize,
      // pageNum:1,
      // pageSize:10,
      operType: "1"
    }
    this.httpService.queryBatchPreCreditDetailList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.batchList = data.resultList;
        this.batchList.forEach(element => {
          element.loanOfficerAdvises = this.transMony(element.loanOfficerAdvises * 10000);
          element.loanSystemAdvises = this.transMony(element.loanSystemAdvises * 10000);
        });
        if (this.batchList.length < 1) {
          this.showFlag8 = true
        } else {
          this.showFlag8 = false;
        }
        this.batchListPageTotalNum = data.total;
      }
    })
  }
  // 金额格式转换
  transMony(param) {
    let result = "";
    if (param) {
      result = this.commfunc.transform(param) + '';
    }
    result = this.decimalPipe.transform(param, '1.2-2');//申请金额
    return result;
  }
