  //作为共同借款人 共借债务
  queryBorrower() {
    let param = {
      pageNum: this.pageNum,
      pageSize: this.pageSize,
      clientNo: this.custNo,
      // clientNo: '5100006072',
      clientFlag: '2',
      tranType: '0',
    }
    this.httpService.BZ_0200300000102_LOAN(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        for (let item of data.infoList) {
          this.httpService.queryTellerById({ tellerId: item.investigatorName }).subscribe(data => {
            item.investigatorName = data.tellerName;
          });
        }
        this.borrowerBusinessItems = data.infoList;
        this.gjzw_choose();
        // if (this.borrowerBusinessItems.length == 0) {
        //   this.showFlag4 = true;//共借债务
        // }
        this.borrowerBusinessItemspageSize = data.pageSize;
        this.borrowerBusinessItemspageNum = data.pageNum;
        this.borrowerBusinessItemstotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }
