import { Component, OnInit,OnChanges,Input,Output,EventEmitter } from '@angular/core';
import {CusViewHttpService} from '../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import { tableMessage,yearRangeBig,yearRangeSmall,CALENDAR_CN } from 'app/@uisftech/common/constant/config';
// import {VetoType,SubVetoType} from '../../../constant/custView.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'bulkpercredit-detail',
  templateUrl: './bulkpercredit-detail.component.html',
  styleUrls: ['./bulkpercredit-detail.component.scss']
})
export class BulkpercreditDetailComponent implements OnInit,OnChanges {
  @Input() inValue:any;
  @Output() outValue=new EventEmitter();
  tabMesg:any=tableMessage;
  bulkpercreditDetailInfo:any[]=[];
  subVetoTypeOptions:any=[];//二级否决类型
  vetoTypeOptions:any=[];//否决类型
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc: Commfunc 
  ) { 
    this.codeVal();
    this.subVetoTypeOptions=this.code['SubVetoType'];
    this.vetoTypeOptions=this.code['VetoType'];
  }

  ngOnInit() {
  }
  ngOnChanges(){
    let param={
      pk:this.inValue.pk
    }
    this.httpService.BZ_0200300002018_LOAN(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        data.infoList.forEach(item => {
          this.httpService.queryOrgById({orgId:item.vetoBankNo}).subscribe(data=>{
            if(data.orgName!=null){
              item.vetoBankNo=data.orgName;              
            }
          });
        });
        this.bulkpercreditDetailInfo=data.infoList;
      }
    });
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
