import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter } from '@angular/core';
import { ReportBean } from './bean/report.bean';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss'],
  providers: [CusViewHttpService]
})
export class ReportComponent implements OnInit {
  reportBean: ReportBean = new ReportBean();
  pageSize: number=10;
  total: number;
  first: number = 0;
  reportList: any;
  msgs: Message[] = [];
  reportDetailCustNm: string;
  reportDetailDisplay: boolean = false;
  inValueReportId: string;
  custNo:string;
  accessWay:any;
  code: any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeVal();
    this.accessWay = this.code['accessWay'];
  }

  ngOnInit() {
    this.reportBean.pageSize = 10;
    this.reportBean.pageNum = 1;
    this.queryReportList()
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.reportBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.reportBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.queryReportList();
  }

  //查询报告列表
  queryReportList() {
    this.custNo = this.commfunc.getSessionData('custNo');
    this.reportBean.custNo = this.custNo;
    this.httpService.compresiveCreditReportListQry(this.reportBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.reportList = data.reportList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    })
  }

  //查看详情按钮
  reportDetail(item) {
    this.reportDetailDisplay = true;
    this.reportDetailCustNm = this.custNo;
    this.inValueReportId = item.reportId;
  }

  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
