import { Component, OnInit,Input,OnChanges} from '@angular/core';
import {CusViewHttpService} from '../../../http/custom-view/custom-view.http.service';
import {Message} from 'primeng/primeng';
import {CreditBusinessDetailBean} from '../../bean/custom-view.bean';
// import {StatusType,BussType,QianFaClass,NewBillStatus,BussStatus,GuaranteeMode,PayType,COUNTRY,CURRENCY} from '../../../constant/custView.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//信用证业务详情
@Component({
  selector: 'credit-business-detail',
  templateUrl: './credit-business-detail.component.html',
  styleUrls: ['./credit-business-detail.component.scss']
})
export class CreditBusinessDetailComponent implements OnInit,OnChanges {
  @Input() inValue:any;
  msgs:Message[]=[]; 
  currencyOptions: any[] = [];//币种  
  creditBusinessDetail:CreditBusinessDetailBean=new CreditBusinessDetailBean();
  guaranteeModeOptions:any[]=[];//担保方式
  qianFaClassOptions:any[]=[];//签发标志
  payTypeOptions:any[]=[];//支付类型
  countryOptions:any[]=[];//国籍
  bussTypeOptions:any[]=[];//业务类型
  statusTypeOptions:any[]=[];//状态
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc: Commfunc    
  ) { 
    this.codeVal();
    this.currencyOptions=this.code['CURRENCY'];
    this.guaranteeModeOptions=this.code['GuaranteeMode'];
    this.qianFaClassOptions=this.code['QianFaClass'];
    this.payTypeOptions=this.code['PayType'];
    this.countryOptions=this.code['COUNTRY'];
    this.bussTypeOptions=this.code['BussType'];
    this.statusTypeOptions=this.code['StatusType'];
    //-------------------------------------------上面是码值
  }

  ngOnInit() {
  }
  ngOnChanges(){
    if(this.inValue){
      this.httpService.BZ_0200300000502_LOAN({contractNo:this.inValue.contractNo}).subscribe(data=>{
        if(data.returnCode.code=='success'){
          this.creditBusinessDetail=data;
          this.httpService.queryTellerById({ tellerId: this.creditBusinessDetail.investigatorName }).subscribe(data => {
            this.creditBusinessDetail.investigatorName = data.tellerName;
          });
          this.httpService.queryTellerById({ tellerId: this.creditBusinessDetail.liablePersonName }).subscribe(data => {
            this.creditBusinessDetail.liablePersonName = data.tellerName;
          });
          this.httpService.queryOrgById({orgId:this.creditBusinessDetail.appBranchNo}).subscribe(data=>{
            if(data.orgName!=null){
              this.creditBusinessDetail.appBranchNo=data.orgName;              
            }
          });
          this.httpService.queryOrgById({orgId:this.creditBusinessDetail.manageBranchId}).subscribe(data=>{
            if(data.orgName!=null){
              this.creditBusinessDetail.manageBranchId=data.orgName;              
            }
          });
        }else{
          data.returnCode.message ? data.returnCode.message : '调用服务失败';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      })
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
